.Burger {
  width: 85%;
  margin: auto;
  height: 300px;
  overflow: hidden visible; /* commenting scroll out for now. makes it look ugly */
  text-align: center;
  font-weight: bold;
  font-size: 1.2rem; /* scales based on font size */
}

@media (min-width: 500px) and (min-height: 400px) {
    .Burger {
      width: 350px;
      height: 300px;
    }
} 

@media (min-width: 500px) and (min-height: 401px) {
    .Burger {
      width: 450px;
      height: 400px;
    }
}


@media (min-width: 1000px) and (min-height: 700px) {
    .Burger {
      width: 700px;
      height: 600px;
    }
}


